import React from "react";
import { Box, Theme, Button, Grid, useModal } from "@faharmony/core";
import {
  PageView,
  useModuleTranslation,
  setAppSideDrawer,
} from "@faharmony/template";
import { Table } from "../components";
import ShuttleExample from "../components/Shuttle";
import Chart from "../components/Chart";
import Accordion from "../components/Accodion";

/**
 * Default page of module
 * @author Siddhant Gupta <siddhant@fasolutions.com>
 */
export const MainPage = () => {
  const t = useModuleTranslation();
  const [Wrapper, open, close] = useModal();
  return (
    <>
      <PageView
        heading={t("moduleName")}
        actions={
          <Box>
            <Button value="Modal" onClick={open} />
            <Button
              value="SideView"
              onClick={() =>
                setAppSideDrawer({
                  caption: "Side",
                  heading: "Summary View",
                  children: <Accordion />,
                })
              }
            />
          </Box>
        }
        tabs={[
          {
            id: "shuttles",
            heading: "TransferShuttle",
            content: (
              <div style={{ padding: Theme.spacing(), height: "100%" }}>
                <ShuttleExample />
              </div>
            ),
          },
          {
            id: "chart",
            heading: "Charts",
            content: (
              <div style={{ padding: Theme.spacing(), height: "100%" }}>
                <Chart />
              </div>
            ),
          },
          {
            id: "table",
            heading: "Table",
            content: (
              <Grid style={{ padding: Theme.spacing(), height: "100%" }}>
                <Table />
              </Grid>
            ),
          },
        ]}
      />

      <Wrapper>
        <Button value="close" onClick={close} />
      </Wrapper>
    </>
  );
};

export default MainPage;
